<template>
<div class="fade-in pocket_list">
  <h1 class="page-header">Meetings</h1>
  <div id="newBtn_container">
    <router-link to="/"><i class="fas fa-home"></i></router-link>
    <button id="newBtn" @click="add = !add"><i class="fas fa-handshake"></i></button>
  </div>
    <div id="createNewMeeting" v-if="add === true">
      <h5 class="new-heading">Enter meeting details</h5>
      <form class="form-group col-md-12">
        <div class="section1">
          <div class="form-section">
            <label for="name">Name of Contact</label>
            <input type="text" name="name" class="form-control" v-model="meeting.name">
          </div>
          <div class="form-section">
            <label for="name">Phone</label>
            <input type="text" name="name" class="form-control" v-model="meeting.phone">
          </div>
          <div class="form-section">
            <label for="name">Email</label>
            <input type="email" name="email" class="form-control" v-model="meeting.email">
          </div>
        </div>
        <div class="section2">
          <div class="form-section">
            <label for="company">Company</label>
            <input type="text" name="company" class="form-control" v-model="meeting.company">
          </div>
          <div class="form-section">
            <label for="location">Location</label>
            <input type="text" name="location" class="form-control" v-model="meeting.location">
          </div>
        </div>
        <div class="section3">
          <div class="form-section">
            <label for="date">Date</label>
            <input type="date" name="date" class="form-control" v-model="meeting.date">
          </div>
          <div class="form-section">
            <label for="time">Time</label>
            <input type="time" name="time" class="form-control" v-model="meeting.time">
          </div>
        </div>
        <div class="section4">
          <div class="form-section">
            <textarea type="text" class="form-control mb-2"  placeholder="additional details (optional)"  v-model="meeting.description"></textarea>
          </div>
          <h4 v-if="feedback">{{ feedback }}</h4>
        </div>
        <div id="btn-div" class="form-section col-sm-12">
          <button class="btn btn-success col-sm-6 mt-2" @click.prevent="save_meeting">Save</button>
          <button class="btn btn-cancel col-sm-6 mt-2" @click.prevent="cancel_meeting">Cancel</button>
        </div>
      </form>
    </div>

    <!-- Display all meetings -->
    <div style="width:100%;" v-else>
      <!--<h4 class="list_title">Meetings</h4>-->
      <ul style="width:100%;" class="meeting-list">
        <li v-for="meeting in sortedMeetings" :key="meeting.name" v-bind:class="{ expired: status(meeting.date) }">
          <div class="meeting-listitem scale-up-ver-top">
            <h4 class="meeting_name" style="color:red;">{{ meeting.name }}</h4>
            <h5>Contact Details</h5>
            <p>phone: {{ meeting.phone }}</p><p>Email: {{ meeting.email }}</p>
            <hr>
            <div class="app_date_container">
              <h5 class="meeting_date">On: {{ meeting.date }}</h5>
              <h5 class="meeting_time">At: {{ meeting.time }}</h5>
            </div>
            <hr>
            <p v-if="meeting.description">Notes: {{ meeting.description }}</p>
            <p v-else>Notes: none</p>
            <h5 class="meeting_status">status: <span style="color:red; font-style: italic;">{{ status(meeting.date) ? 'expired' : 'active' }}</span></h5>
            <label @click="remove_meeting(meeting.id)"><i class="fas fa-trash icon-trash"/></label>
          </div>
        </li>
      </ul>
    </div>
</div>
</template>

<script>
import { db } from "@/config/db.js";
import _ from "lodash";

export default {
  name: "Meetings",
  data() {
    return {
      add: false,
      meeting: {
        name: "",
        email: "",
        phone: "",
        date: "",
        time: "",
        company: "",
        location: "",
        description: ""
      },
      meetings: [],
      feedback: ""
    };
  },
  methods: {
    show_add() {
      this.add = true;
    },
    save_meeting() {
      if (this.meeting.name != "" || this.meeting.date != "") {
        if (
          !localStorage.getItem("myorganiser_userId") ||
          localStorage.getItem("myorganiser_userId") === ""
        ) {
          this.$router.push("/");
        }
        // Save meeting to firebase
        db
          .collection("Meetings")
          .add({
            user_id: localStorage.getItem("myorganiser_userId"),
            name: this.meeting.name.toUpperCase(),
            email: this.meeting.email,
            phone: this.meeting.phone,
            date: this.meeting.date,
            time: this.meeting.time,
            company: this.meeting.company,
            location: this.meeting.location,
            description: this.meeting.description
          })
          .then(() => {
            this.meeting.name = "";
            this.meeting.date = "";
            this.meeting.description = "";
            this.meeting.time = null;
            //this.meeting = null;
            this.feedback = "";
            this.add = false;
          })
          .catch(err => {
            console.log("Error saving meeting: " + err.message);
          });
      } else {
        this.feedback = "At least contact name and date required";
      }
    },
    cancel_meeting() {
      this.$router.push("/");
    },
    remove_meeting(id) {
      db
        .collection("Meetings")
        .doc(id)
        .delete();
    },
    status(d) {
      return new Date(d) < Date.now();
    }
  },
  firestore() {
    return {
      meetings: db
        .collection("Meetings")
        .where("user_id", "==", localStorage.getItem("myorganiser_userId"))
    };
  },
  computed: {
    sortedMeetings: function() {
      return _.orderBy(this.meetings, "date", "desc");
    }
  },
  mounted() {}
};
</script>

